"use client";

import {
  signInWithFacebook,
  signOutWithFacebook,
} from "@/app/api/auth/actions";
import { auth, signIn, signOut } from "@/app/api/auth/auth";
import { useRouter } from "next/navigation";

export const SignIn = async () => {
  const session = await auth();

  const router = useRouter();

  const handleSignIn = async () => {
    await signIn("facebook");
    router.push("/"); // Redirect to root after successful sign-in
  };

  const handleSignOut = async () => {
    await signOut();
    router.push("/"); // Redirect to root after successful sign-out
  };

  return (
    <>
      {session ? (
        <form
          onSubmit={async (e) => {
            e.preventDefault(); // Prevent page reload
            await handleSignOut(); // Handle sign-out
          }}
        >
          <button type="submit">Signout with Facebook</button>
        </form>
      ) : (
        <form
          onSubmit={async (e) => {
            e.preventDefault(); // Prevent page reload
            await handleSignIn(); // Handle sign-in
          }}
        >
          <button type="submit">Signin with Facebook</button>
        </form>
      )}
    </>
  );
};
